/* tonconnect-styles.css */
.connect-button {
  background-color: #0098EA;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.connect-button:hover {
  background-color: #007bb5;
}

.disconnect-button {
  background-color: #FF4C4C;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.disconnect-button:hover {
  background-color: #cc0000;
}
